import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/vv_menu.png';
import OptionsImage from './images/vv_options.png';
import SaveImage from './images/vv_save.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} `,
    <>
      <Typography>
        {title} 
      </Typography>
    </>,
  ],
  [
    ``,
    <>
      <img src={MenuImage} alt="" width="229" height="226" />
      <Typography>
        
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={OptionsImage} alt="" width="505" height="307" />
      <Typography>
        
      </Typography>
      <ol>
      <li>UTF-8</li>
      <li>
        {
            tmpPath ?
              <>
                 <Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> 
              </>
              :
              <>
                 forcepser.exetmp
                <Typography variant="body2">
                  tmp
                </Typography>
              </>
        }
      </li>
      <li></li>
      <li>txt</li>
      </ol>
    </>,
  ],
  [
    '',
    <>
      <Typography>
        
      </Typography>
    </>,
  ],
  [
    ': ',
    <>
      <img src={SaveImage} alt="" width="380" height="169" />
      <Typography>
        
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonVOICEVOX(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} `} title={`${title} `} steps={steps} />
  );
}
