import { Typography } from '@material-ui/core';
import React from 'react';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

import SettingsIcon from '@material-ui/icons/Settings';
import HomeIcon from '@material-ui/icons/Home';
import GetAppIcon from '@material-ui/icons/GetApp';

import MenuImage from './images/coeiroink_menu.png';
import OptionsImage from './images/coeiroink_options.png';
import SaveImage from './images/coeiroink_save.png';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} `,
    <>
      <Typography>
        {title} 
      </Typography>
    </>,
  ],
  [
    ``,
    <>
      <img src={MenuImage} alt="" width="306" height="181" />
      <Typography>
         <SettingsIcon /> 
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={OptionsImage} alt="" width="417" height="313" />
      <Typography>
        
      </Typography>
      <ol>
      <li></li>
      <li>
        {
            tmpPath ?
              <>
                <Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> 
              </>
              :
              <>
                forcepser.exetmp
                <Typography variant="body2">
                  tmp
                </Typography>
              </>
          }

      </li>
      <li>txt</li>
      <li>txtUTF-8</li>
      </ol>
    </>,
  ],
  [
    '',
    <>
      <Typography>
         <HomeIcon /> 
      </Typography>
    </>,
  ],
  [
    '[] ',
    <>
      <img src={SaveImage} alt="" width="123" height="223" />
      <Typography>
         <GetAppIcon /> 
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonVOICEVOX(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} `} title={`${title} `} steps={steps} />
  );
}
