import { Typography } from '@material-ui/core';
import React from 'react';
import StepperDialogButton from './StepperDialogButton';
import FileImage from './images/bouyomi_file.png';
import ConfigImage from './images/bouyomi_config.png';
import SaveImage from './images/bouyomi_save.png';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    ' ',
    <>
      <Typography>
        <a href="https://github.com/yukimaru73/Plugin_Bouyomichan_wav_and_txt/releases" target="_blank" rel="noreferrer">Plugin_Bouyomichan_wav_and_txt.dll</a> <br />
        Wave 
      </Typography>
    </>
  ],
  [
    '',
    <>
      <img src={FileImage} alt="" width="669" height="533" />
      <Typography>
         <Typography style={{ fontFamily: "monospace" }} display="inline">Plugin_Bouyomichan_wav_and_txt.dll</Typography> {title} 
      </Typography>
    </>
  ],
  [
    `${title} `,
    <>
      <Typography>
        {title} 
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={ConfigImage} alt="" width="314" height="418" />
      <Typography>
        
      </Typography>
      <ol>
        <li> </li>
        <li> </li>
        <li></li>
      </ol>
    </>
  ],
  [
    ``,
    <>
      <img src={SaveImage} alt="" width="634" height="220" />
      {
        tmpPath ?
          <>
            <Typography>
              01)<Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> 
            </Typography>
          </>
          :
          <>
            <Typography>
              01)forcepser.exetmp
            </Typography>
            <Typography variant="body2">
              tmp
            </Typography>
          </>
      }
    </>,
  ],
  [
    '',
    <>
      <Typography>
        OK
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonBouyomi(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} `} title={`${title} `} steps={steps} />
  );
}
