import StepperDialogButton from './StepperDialogButton';

const getSteps = (title: string): [string, React.ReactNode][] => [
  [
    `${title} `,
    <>
      <Typography>
        {title} 
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={MenuImage} alt="" width="305" height="129" />
      <Typography>
          ...
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={OptionsImage} alt="" width="642" height="165" />
      <Typography>
      
      </Typography>
      <ol>
        <li>WAVF4</li>
      </ol>
    </>,
  ],
  [
    '',
    <>
      <Typography>
        
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={HeaderImage} alt="" width="245" height="251" />
      <Typography>
      
      </Typography>
      <ol>
        <li></li>
      </ol>
    </>,
  ],
  [
    ': ',
    <>
      <img src={SaveImage} alt="" width="453" height="240" />
      <Typography>
        Shift + Ctrl + Alt + S<br />
        
      </Typography>
      <ul>
      <li>1: {title} </li>
      <li>2: </li>
      <li>3:  {title} 1.1.5.1 </li>
      </ul>
    </>,
  ],
];

export default function StepperSetUpButtonCeVIO(props: {
  title: string;
}) {
  const { title } = props;
  const steps = React.useMemo(() => getSteps(title), [title]);
  return (
    <StepperDialogButton caption={`${title} `} title={`${title} `} steps={steps} />
  );
}
