import { Typography } from '@material-ui/core';
import React from 'react';
import SaveImage from './images/voiceroidutil_save.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} `,
    <>
      <Typography>
        {title} 
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={SaveImage} alt="" width="402" height="228" />
      <Typography>
        (2)(A)
      </Typography>
      <ol>
        <li>
          {
            tmpPath ?
              <>
                (D)...<Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> 
              </>
              :
              <>
                (D)...forcepser.exetmp
                <Typography variant="body2">
                  tmp
                </Typography>
              </>
          }
        </li>
        <li>__</li>
        <li>(T)</li>
        <li> UTF-8 (U)</li>
        <li>AviUtl(.exo)(E)</li>
      </ol>
    </>,
  ],
];

export default function StepperSetUpButtonVoiceroidUtil(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} `} title={`${title} `} steps={steps} />
  );
}
