import { Typography } from '@material-ui/core';
import React from 'react';
import MenuImage from './images/aitalk6_menu.png';
import SaveTabImage from './images/aitalk6_save.png';
import VoiceTabImage from './images/aitalk6_voice.png';
import StepperDialogButton from './StepperDialogButton';
import * as ForcepserConfig from './ForcepserConfig';

const getSteps = (title: string, tmpPath: string): [string, React.ReactNode][] => [
  [
    `${title} `,
    <>
      <Typography>
        {title} 
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={MenuImage} alt="" width="299" height="118" />
      <Typography>
        (T)(P)...
      </Typography>
    </>,
  ],
  [
    '',
    <>
      <img src={VoiceTabImage} alt="" width="469" height="290" />
      <Typography>
        
      </Typography>
      <ol>
        <li></li>
        <li></li>
        <li></li>
      </ol>
    </>,
  ],
  [
    '',
    <>
      <img src={SaveTabImage} alt="" width="479" height="633" />
      <Typography>
        
      </Typography>
      <ol>
        <li>WAVE48000Hz 16bit PCM</li>
        <li>UTF-8</li>
        <li></li>
        <li>
          {
            tmpPath ?
              <>
                 <Typography style={{ fontFamily: "monospace" }} display="inline">{tmpPath}</Typography> 
              </>
              :
              <>
                 forcepser.exetmp
                <Typography variant="body2">
                  tmp
                </Typography>
              </>
          }
        </li>
        <li> <Typography style={{ fontFamily: "monospace" }} display="inline">{"{yyyyMMdd}{HHmmss}_{VoicePreset}_{Text}"}</Typography> </li>
      </ol>
    </>,
  ],
  [
    '',
    <>
      <Typography>
        OK
      </Typography>
    </>,
  ],
];

export default function StepperSetUpButtonAITalk6(props: {
  title: string;
}) {
  const { title } = props;
  const [tmpPath, setTmpPath] = React.useState("");
  const steps = React.useMemo(() => getSteps(title, tmpPath), [title, tmpPath]);
  React.useEffect(() => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.getTmpPath().then(setTmpPath);
  }, []);
  return (
    <StepperDialogButton caption={`${title} `} title={`${title} `} steps={steps} />
  );
}