export const modifiers: {
  [key: string]: [string, string],
} = {
  // eslint-disable-next-line no-template-curly-in-string
  aiTalkRuby: ['', 'text = re.gsub(text, "(.*?).*?", "${1}")'],
  aiTalkVoicePresetTag: ['', 'text = re.gsub(text, "^.*?", "")'],

  // eslint-disable-next-line no-template-curly-in-string
  cevioRubyJP: ['', 'text = re.gsub(text, "?([^]*).*?", "${1}")'],
  // eslint-disable-next-line no-template-curly-in-string
  cevioRubyEN: ['', 'text = re.gsub(text, "\\\\[.*?\\\\]", "")'],
};

const extractFileName = (s: string) => {
  const m = s.match(/[^\\]+$/);
  if (m) {
    return m[0];
  }
  return "*.exe";
};

const aiTalkApplication = (productName: string, characterName: string, path: string, nickName: string): TApplicationTemplate => ({
  name: productName,
  exeName: extractFileName(path),
  bin: `C:\\Program Files (x86)\\${path}`,
  binPlaceholder: "",
  file: `${nickName}_*.wav`,
  folder: "%TEMPDIR%",
  modifiers: ['aiTalkRuby'],
  asasMode: 'autosave',
  useFairyCall: false,
  editableCommonDir: false,
  commonDir: '%TEMPDIR%',
  commonDirPlaceholder: "",
  characterTemplates: [{
    onlyOne: true,
    name: characterName,
    editableName: false,
    nickName: nickName,
    file: `${nickName}_*.wav`,
    editableFile: false,
    encoding: "sjis",
    dir: "",
    editableDir: false,
    useCommonDir: true,
    preset: "",
    detectType: 'file',
    modifiers: ['aiTalkRuby'],
  }],
  stepperSetUp: <StepperSetUpButtonAITalk title={productName} />,
});
const voiceroidApplication = (productName: string, characterName: string, path: string, nickName: string): TApplicationTemplate =>
  aiTalkApplication('VOICEROID ' + productName, characterName, 'AHS\\VOICEROID+\\' + path + '\\VOICEROID.exe', nickName);
