import React, { useState, useCallback, useEffect } from 'react';
import { Box, Card, CardHeader, CardContent, Collapse, Typography } from '@material-ui/core';
import { CheckCircle as GoodIcon, Error as BadIcon, WebAsset as FileIcon, Folder as FolderIcon } from '@material-ui/icons';
import * as ActionButtons from './ActionButtons';
import * as Character from './Character';
import { TApplication, TApplicationPartial, TCharacterTemplate } from './Types';
import * as Templates from './Templates';
import TextFieldButton from './TextFieldButton';
import TextFieldWithMenu from './TextFieldWithMenu';
import AddCharacterButton from './AddCharacterButton';
import SwapAnimator from './SwapAnimator';
import * as ForcepserConfig from './ForcepserConfig';

export type TActions =
  { type: 'move-up' | 'move-down' | 'delete', index: number } |
  { type: 'update', index: number, v: TApplicationPartial } |
  { type: 'add-character', index: number, v: TCharacterTemplate } |
  { type: 'character', index: number, action: Character.TActions };

const monospaceStyle = { style: { fontFamily: 'monospace' } };
const iconStyle = { verticalAlign: "middle", fontSize: "1.25rem" };
const appBinIcon = <FileIcon />;
const folderIcon = <FolderIcon />;
const appCommonDirIsRequired = <Typography color="error" variant="caption"><BadIcon style={iconStyle} /> </Typography>;
const appCommonDirFound = <Typography color="primary" variant="caption"><GoodIcon style={iconStyle} /> </Typography>;
const appCommonDirNotFound = <Typography color="error" variant="caption"><BadIcon style={iconStyle} /> </Typography>;

const appBinFound = <Typography color="primary" variant="caption"><GoodIcon style={iconStyle} /> </Typography>;
const appBinNotFound = <Typography color="error" variant="caption"><BadIcon style={iconStyle} /> </Typography>;
const appBinIsRequired = <Typography color="error" variant="caption"><BadIcon style={iconStyle} /> exe</Typography>;
const appBinIsNotExe = <Typography color="error" variant="caption"><BadIcon style={iconStyle} /> exe</Typography>;

const specialFolders: [string, string][] = [
  [' tmp ', '%TEMPDIR%'],
  ['AviUtl ', '%PROJECTDIR%'],
  ['C:\\Users\\', '%PROFILE%'],
  ['', '%DESKTOP%'],
  ['', '%MYDOC%'],
];

type Props = {
  app: TApplication;
  index: number;
  length: number;
  dispatch: React.Dispatch<TActions>;
  swapping: boolean;
};

export const Application = React.forwardRef<HTMLDivElement, Props>(function Application(props, ref) {
  const { app, index, length, dispatch, swapping } = props;
  const [appCommonDirError, setAppCommonDirError] = useState<React.ReactNode>(undefined);
  const [appBinError, setAppBinError] = useState<React.ReactNode>(undefined);
  const [swappingKeys, setSwappingKeys] = useState<string[]>([]);
  const onClearSwapping = useCallback(() => setSwappingKeys([]), [setSwappingKeys]);
  const stepperSetUp = Templates.find(app.name)?.stepperSetUp;
  const [collapsed, setCollapsed] = useState(true);
  const [animating, setAnimating] = useState(false);
  const [animated, setAnimated] = useState(false);
  const handleActionButtons = useCallback((a: ActionButtons.TActions) => {
    if (a.type === "delete") {
      setAnimated(false);
    } else {
      dispatch({ type: a.type, index });
    }
  }, [dispatch, index]);
  const handleAddCharacter = useCallback((ct: TCharacterTemplate) => dispatch({ type: 'add-character', index, v: ct }), [dispatch, index]);
  const handleCharacterDispatch = useCallback((a: Character.TActions) => dispatch({ type: 'character', index, action: a }), [dispatch, index]);
  const handleChangeBin = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { bin: e.target.value.replaceAll(/"|^\s+|\s+$/g,"") } }), [dispatch, index]);
  const handleChangeCommonDir = useCallback((e: React.ChangeEvent<HTMLInputElement>) => dispatch({ type: 'update', index, v: { commonDir: e.target.value.replaceAll(/"|^\s+|\s+$/g,"") } }), [dispatch, index]);
  const handleChangeCommonDirFromTemplate = useCallback((s: string) => dispatch({ type: 'update', index, v: { commonDir: s } }), [dispatch, index]);
  const handleClickFileSelectButton = useCallback((e: React.MouseEvent<HTMLButtonElement>) => {
    if (!ForcepserConfig.hasAPI()) {
      return;
    }
    ForcepserConfig.chooseFile({
      path: app.bin,
      filename: Templates.find(app.name)?.exeName || "*.exe",
      title: `${app.name}`,
      filter: "(*.exe)|*.exe",
    }).then(path => {
      dispatch({ type: 'update', index, v: { bin: path } });
    }).catch(e => {
      if (e === "abort") {
        return;
      }
      return e;
    });
  }, [dispatch, app.bin, app.name, index]);
  const handleCollapseAnimating = useCallback(() => setAnimating(true), []);
  const handleCollapseEntered = useCallback(() => setAnimating(false), []);
  const handleCollapseExited = useCallback(() => dispatch({ type: "delete", index }), [dispatch, index]);
  useEffect(() => {
    if (!animated) {
      setCollapsed(f => !f);
      setAnimated(true);
    }
  }, [animated]);
  useEffect(() => {
    if (app.bin === "") {
      setAppBinError(appBinIsRequired);
      return;
    }
    if (!ForcepserConfig.hasAPI()) {
      setAppBinError(app.bin.match(/\\.*?\.exe$/i) ? undefined : appBinIsNotExe);
      return;
    }
    ForcepserConfig.exists(app.bin).then(exists => setAppBinError(exists ? appBinFound : appBinNotFound));
  }, [app.bin]);
  useEffect(() => {
    if (app.commonDir === "") {
      setAppCommonDirError(appCommonDirIsRequired);
      return;
    }
    if (!ForcepserConfig.hasAPI()) {
      setAppCommonDirError(undefined);
      return;
    }
    ForcepserConfig.exists(app.commonDir).then(exists => setAppCommonDirError(exists ? appCommonDirFound : appCommonDirNotFound));
  }, [app.commonDir]);
  return (
    <div ref={ref}>
      <Collapse in={!collapsed} onExiting={handleCollapseAnimating} onEntering={handleCollapseAnimating} onEntered={handleCollapseEntered} onExited={handleCollapseExited}>
        <Box my={2}>
          <Card elevation={3}>
            <CardHeader
              action={<ActionButtons.ActionButtons name={`${app.name}`} index={index} length={length} disabled={animating || swapping} dispatch={handleActionButtons} />}
              title={app.name}
            />
            <CardContent>
              <form noValidate autoComplete="off">
                {app.asasMode === 'disable' ? undefined : (<Box my={2}>
                  <TextFieldButton
                    size="small"
                    label=""
                    placeholder={app.binPlaceholder}
                    tooltip=""
                    buttonPosition="end"
                    icon={appBinIcon}
                    hideIcon={!ForcepserConfig.hasAPI()}
                    disabled={animating || swapping}
                    value={app.bin}
                    onClick={handleClickFileSelectButton}
                    onChange={handleChangeBin}
                    inputProps={monospaceStyle}
                    error={appBinError !== undefined}
                    helperText={appBinError}
                  />
                </Box>)}
                {!app.editableCommonDir ? undefined : (<Box my={2}>
                  <TextFieldWithMenu
                    size="small"
                    label=""
                    placeholder={app.commonDirPlaceholder}
                    tooltip=""
                    disabled={animating || swapping}
                    inputProps={monospaceStyle}
                    icon={folderIcon}
                    menuItems={specialFolders}
                    value={app.commonDir}
                    onChange={handleChangeCommonDir}
                    onSelect={handleChangeCommonDirFromTemplate}
                    error={appCommonDirError !== undefined}
                    helperText={appCommonDirError}
                  />
                </Box>)}
              </form>
              <Box my={2}>
                {stepperSetUp}
              </Box>
              <Box my={2}>
                <Typography variant="subtitle1" component="h3"></Typography>
              </Box>
              <SwapAnimator onStart={setSwappingKeys} onEnd={onClearSwapping}>
                {app.characters.map((c, cindex) => (
                  <Character.Character
                    ref={React.createRef()}
                    key={c.gkey}
                    appName={app.name}
                    character={c}
                    index={cindex}
                    length={app.characters.length}
                    disabled={animating || swapping}
                    swapping={swappingKeys.includes(`${c.gkey}`)}
                    dispatch={handleCharacterDispatch}
                  />
                ))}
              </SwapAnimator>
              {app.characters.length === 1 && app.characters[0].onlyOne ? [] : <AddCharacterButton appName={app.name} disabled={animating || swapping} onAdd={handleAddCharacter} />}
            </CardContent>
          </Card>
        </Box>
      </Collapse>
    </div>
  );
});