
import React, { useState } from 'react';
import { Box, Dialog, DialogTitle, DialogContent, Typography } from '@material-ui/core';

let gidCounter = 0;

export default function OverwriteGuardDialog(props: {modified?: boolean}) {
  const { modified } = props;
  const [gid] = useState(() => `overwrite-guard-dialog-${++gidCounter}`);
  const [open, setOpen] = useState(true);
  const handlePreviewClose = () => setOpen(false);
  return (
    <>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        id={gid + "-dialog"}
        fullWidth
        maxWidth="lg"
        onClose={handlePreviewClose}
        aria-labelledby={gid + "-dialog-title"}
        open={open}
      >
        <DialogTitle id={gid + "-dialog-title"}>
          
        </DialogTitle>
        <DialogContent>
          <Typography>
            {modified ? <>
              <Typography color="error" display="inline"></Typography>
            </> : <>
              <Typography color="error" display="inline"></Typography>
            </>}
            <Box my={2}>
              
            </Box>
            <Box my={2}>
              <Typography color="error" display="inline"></Typography>
            </Box>
          </Typography>
        </DialogContent>
      </Dialog>
    </>
  );
}
